@import '../common/mixins.scss';

/* Popup CSS */

.pop .modal {
    z-index: 2;
    opacity: 0;
    visibility: hidden;
    position: fixed;
    width: 100%;
    height: 100%;
    background: rgba(0, 0, 0, 0.5);
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    transition: opacity 500ms ease-in-out;
}

.pop .modal__inner {
    transform: translate(-50%, -50%) scale(0.75);
    top: 50%;
    left: 50%;
    width: 50%;
    background: rgb(201, 64, 100);
    padding: 30px;
    position: absolute;
    color: black;
    background: #FF1654;
    background-image: -webkit-linear-gradient(top, #FF1654, rgb(201, 64, 100));
    background-image: -moz-linear-gradient(top, #FF1654, rgb(201, 64, 100));
    background-image: -ms-linear-gradient(top, #FF1654, rgb(201, 64, 100));
    background-image: -o-linear-gradient(top, #FF1654, rgb(201, 64, 100));
    background-image: linear-gradient(to bottom, #FF1654, rgb(201, 64, 100));
    border-radius: 20px;
    padding: 1rem;
    max-height: calc(100vh - 3rem);
    overflow: scroll;
}

@include sm {
    .pop .modal__inner {
        width: calc(100% - 6rem);
    }
}

.pop label {
    display: inline-block;
    cursor: pointer;
}

.pop label.open {
    color: turquoise;
    transition: color 200ms ease-in-out;
    text-decoration: underline;
}

.pop label.open:hover {
    color: paleturquoise;
}

.pop input {
    display: none;
}

.pop input:checked+.modal {
    opacity: 1;
    visibility: visible;
}

.pop input:checked+.modal .modal__inner {
    transform: translate(-50%, -50%) scale(1);
    transition: all 200ms ease-in-out;
}

.pop input:checked+.modal .modal__inner p {
    font-size: 1.25rem;
    line-height: 125%;
}

.pop input:checked+.modal label {
    background: #49A062;
    transition: all 200ms ease-in-out;
    padding: 0.5rem 1rem;
    color: rgb(201, 64, 100);
    border-radius: 0.5rem;
}

.pop input:checked+.modal label:hover {
    background: rgb(201, 64, 100);
}

/* End of popup CSS */