@import '../common/mixins.scss';

.KeyBoard {
  height: 12rem;
}

.Row {
  display: flex;
  justify-content: center;
}

.Key {
  display: inline;
  border-radius: 0.2rem;
  margin: 0.5rem;
  background: #eefa4b;
  background-image: -webkit-linear-gradient(top, #eefa4b, #c1e010);
  background-image: -moz-linear-gradient(top, #eefa4b, #c1e010);
  background-image: -ms-linear-gradient(top, #eefa4b, #c1e010);
  background-image: -o-linear-gradient(top, #eefa4b, #c1e010);
  background-image: linear-gradient(to bottom, #eefa4b, #c1e010);
  font-size: 20px;
  text-decoration: none;
  width: 4rem;
  height: 4rem;
  text-align: center;
  cursor: pointer;
  font-weight: bold;
  box-shadow: 0 0 10px #000;
  display: flex;
  justify-content: center;
  align-items: center;
  &.Disabled {
    background: gray;
    cursor: not-allowed;
    box-shadow: none;
    color: white;
    transition: background 0.5s, box-shadow 0.5s;
  }

  @include sm {
    margin: 0.1rem;
    height: 10vw;
    width: 10vw;
    box-shadow: none;
  }

  @include lg {
    margin: 0.2rem;
    height: 3rem;
    width: 3rem;
  }
}

.Key:active,
.Key.active {
  box-shadow: inset 0px 5px 10px 0px rgba(0, 0, 0, 0.5);
}