html {
    background: radial-gradient(rgb(204, 229, 17), rgb(35,157,15));
    user-select: none;
    background-repeat: no-repeat;
    min-height: 100vh;
    padding: 0;
    height: 100%;
    background-attachment: fixed;
}

.App {
    text-align: center;
    min-height: 100vh;
    display: flex;
    flex-direction: column;
    justify-content: space-evenly;
}

header {
    background-color: #c1e01088;
    position: fixed;
    width: 100%;
    top: 0;
    height: 60px;
}

.HeaderContent {
    max-width: 720px;
    display: flex;
    align-items: center;
    margin: auto;

    .progress {
        text-transform: uppercase;
        font-size: 14px;
        margin-right: 5px;
    }
}

header h1 {
    font-size: 1.5rem;
    text-align: left;
    margin-right: auto;
    padding: 10px;
    font-weight: normal;
    text-align: left;
    margin-left: 5px;
    padding-left: 0;
    line-height: 1rem;
}

.Word {
    display: flex;
    justify-content: center;
    margin-top: 100px;
}

.WordChar {
    width: 3rem;
    height: 3rem;
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: 2rem;
    font-weight: bold;
    margin: 1px;

    background: #eefa4b;
    background-image: -webkit-linear-gradient(top, #eefa4b, #c1e010);
    background-image: -moz-linear-gradient(top, #eefa4b, #c1e010);
    background-image: -ms-linear-gradient(top, #eefa4b, #c1e010);
    background-image: -o-linear-gradient(top, #eefa4b, #c1e010);
    background-image: linear-gradient(to bottom, #eefa4b, #c1e010);
    box-shadow: inset 0px 5px 10px 0px rgba(0, 0, 0, 0.5);
}

.WordChar.Known {
    box-shadow: none;
    animation: zoom-in-zoom-out 0.5s ease forwards;
}

.Progress {
    text-transform: uppercase;
    font-weight: bold;
    line-height: 2rem;
}

.Result {
    button {
        padding: 10px 10px;
        background: green;
        color: white;
        border-radius: 1rem;
        border: none;
        cursor: pointer;
        font-weight: bold;
    }
}

@keyframes zoom-in-zoom-out {
    0% {
        transform: scale(0.1, 0.1);
    }

    100% {
        transform: scale(1, 1);
    }
}